.disk {
    margin-top: 20px;

    .disk__btns {
        display: flex;
    }

    .disk__create {
        margin-left: 10px;
    }

    .disk__upload-input {
        display: none;
    }
    .disk__upload-label {
        border: 2px dashed #566855;
        padding: 5px 10px;
        cursor: pointer;
        margin-left: 10px;
    }
    .disk__select {
        margin-left: auto;
    }

    .disk__list, .disk__plate {
        height: 20px;
        width: 20px;
        border: none;
        outline: none;
        cursor: pointer;
        background: no-repeat center center;
        background-size: contain;
        margin: 0 10px;
    }
    .disk__list {
        background-image: url('../../assets/img/list.png');
    }
    .disk__plate {
        background-image: url('../../assets/img/plate.png');
    }
}

.drop__area {
    width: 100%;
    height: calc(100vh - 50px - 40px);
    margin: 20px;
    border: dashed 2px #566855;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
}

.popup {
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,0.2);
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    .popup__content {
        width: 400px;
        background: white;
        padding: 20px;
        border-radius: 12px;
        display: flex;
        flex-direction: column;
    }

    .popup__create {
        align-self: flex-end;
    }

    .popup__header {
        display: flex;
        justify-content: space-between;
    }

    input {
        margin: 20px 0;
    }
}

.loader1 {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 50px);
}

.loader {
    width: 48px;
    height: 48px;
    border: 5px solid #566855;
    border-bottom-color: transparent;
    border-radius: 50%;
    display: inline-block;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
    }

    @keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
    } 