.fileplate {
    display: flex;
    flex-wrap: wrap;
    margin: 20px 0;
    margin-left: 20px;

}

.filelist {
    margin: 20px 0;

    .filelist__header {
        display: grid;
        grid-template-columns: 1fr 4fr repeat(4, 1fr);
    }

    .filelist__name {
        grid-column-start: 2;
    }
    .filelist__date {
        grid-column-start: 5;
    }
    .filelist__size {
        grid-column-start: 6;
        justify-self: center;
    }
}

.file-enter-active {
   animation: file-enter 500ms forwards;
}

@keyframes file-enter {
    0% {
      opacity: 0;
      transform: translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.div {
    display: flex;
    justify-content: center;
    align-items: center;
    height: calc(100vh - 50px);
}